

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.lorem{
  visibility: hidden;
}
.event-calendar {
  .datepicker-inner{
    width: 440px;
  }
  .datepicker-body{
    span{
      width: 60px;
      height: 60px;
      vertical-align: top;
    }
    .event{
      color: #E56700;
    }
    .low{
      color: red;
      font-weight: bold;
    }
    .datepicker-monthRange span{
      width: 100px;
      height: 100px;
      vertical-align: middle;
      line-height: 100px;
      // font-weight: 600;
    }
  }

}
