
@mixin clearfix() {

  &:before,
  &:after {
    content: " "; // 1
    display: table; // 2
  }

  &:after {
    clear: both;
  }
}

.datepicker {
  font-size: 14px;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  box-sizing: border-box;
  -webkit-tap-highlight-color: #0000;
  -webkit-font-smoothing: antialiased;
  // color: #2c3e50;
  color: rgba(0, 0, 0, 0.6509803921568628);
  position: relative;
  display: inline-block;

  .form-control {
    box-sizing: border-box;
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857;
    color: #555;
    background-color: #fff;
    background-image: none;
    // border: 1px solid #ccc;
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    box-shadow: none;

    &:hover,
    &:focus {
      outline: 0;
      border-color: #40a9ff;
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
        0 0 8px rgba(102, 175, 233, 0.6);
    }
  }

  button.close {
    padding: 0;
    cursor: pointer;
    background: #0000;
    border: 0;
    -webkit-appearance: none;
    float: right;
    font-size: 21px;
    font-weight: bold;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    opacity: 0.2;
    filter: alpha(opacity=20);
  }
}

input.datepicker-input.with-reset-button {
  padding-right: 25px;
}

.datepicker>button.close {
  position: absolute;
  top: 0;
  right: 0;
  outline: none;
  z-index: 2;
  display: block;
  width: 34px;
  height: 34px;
  line-height: 34px;
  text-align: center;
}

.datepicker>button.close:focus {
  opacity: 0.2;
}

.datepicker-wrapper {
  position: absolute;
  z-index: 1000;
}

.datepicker-popup {
  // border: 1px solid #ccc;
  border: 1px solid #fff;
  border-radius: 5px;
  background: #fff;
  margin-top: 2px;
  // box-shadow: 0 6px 12px rgba(0,0,0,0.175);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  @include clearfix;
}

.datepicker-inner {
  width: 218px;
  float: left;
}

.datepicker-body {
  padding: 10px 10px;
  text-align: center;

  p {
    margin: 0 0 10px;
  }
}

.datepicker-ctrl p,
.datepicker-ctrl span,
.datepicker-body span {
  display: inline-block;
  width: 28px;
  line-height: 28px;
  height: 28px;
  // border-radius: 4px;
}

.datepicker-ctrl p {
  width: 65%;
}

.datepicker-ctrl span {
  position: absolute;
}

.datepicker-body span {
  text-align: center;
}

.datepicker-monthRange span {
  width: 48px;
  height: 50px;
  line-height: 45px;
}

.datepicker-item-disable {
  background-color: white !important;
  cursor: not-allowed !important;
}

.decadeRange span:first-child,
.decadeRange span:last-child,
.datepicker-item-disable,
.datepicker-item-gray {
  color: #999;
}

.datepicker-dateRange-item-active:hover,
.datepicker-dateRange-item-active {
  background: rgb(50, 118, 177) !important;
  color: white !important;
}

.datepicker-item-disabled {
  color: #aaa;
  text-decoration: line-through;
}

.datepicker-monthRange {
  margin-top: 10px;
}

.datepicker-monthRange span,
.datepicker-ctrl span,
.datepicker-ctrl p,
.datepicker-dateRange span {
  cursor: pointer;
}

.datepicker-monthRange span:hover,
.datepicker-ctrl p:hover,
.datepicker-ctrl i:hover,
.datepicker-dateRange span:hover,
.datepicker-dateRange-item-hover {
  background-color: #eeeeee;
}

.datepicker-dateRange {

  .daytoday-start,
  .daytoday-start:hover,
  .daytoday-end,
  .daytoday-end:hover {
    background: rgb(50, 118, 177) !important;
    color: white !important;
  }
}

.datepicker-dateRange .daytoday-range,
.datepicker-dateRange .daytoday-range:hover {
  background-color: #ddd;
}

.datepicker-weekRange span {
  font-weight: bold;
}

.datepicker-label {
  background-color: #f8f8f8;
  font-weight: 700;
  padding: 7px 0;
  text-align: center;
}

.datepicker-ctrl {
  position: relative;
  /*height: 30px;*/
  line-height: 30px;
  font-weight: bold;
  text-align: center;
  top: 3px;
}

.month-btn {
  font-weight: bold;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.datepicker-preBtn {
  left: 2px;
  width: 20px !important;
  height: 20px !important;
  background-repeat: no-repeat;
  background-size: 18px;

}

.datepicker-nextBtn {
  right: 2px;
  width: 20px !important;
  height: 20px !important;
  background-repeat: no-repeat;
  background-size: 18px;
}

.calendaricon-angle-left {
  top: 10px;
  background-image: url(data:image/svg+xml;base64,);
}

.calendaricon-angle-right {
  top: 10px;
  background-image: url(data:image/svg+xml;base64,);
}
